import React, { Component } from 'react';

import AppHeader from '../app-header';
import SearchPanel from '../search-panel';
import TodoList from '../todo-list';
import ItemStatusFilter from '../item-status-filter';
import ItemAddForm  from "../item-add-form";
import './app.css';



class App  extends Component {

  maxId = 100;

  state = { 
      todoData: [
        { label: 'Drink Coffee', important: false, done: false,  id: 1 },
        { label: 'Make Awesome App', important: true, done: false, id: 2 },
        { label: 'Have a lunch', important: false, done: false, id: 3 }
    ],
    valueOfInput: "",
    allItems: true,
    activeItems: false,
    aoneItems: false
  }

  deleteItem = (id) => {
    this.setState((prevState) => {
      const idx = prevState.todoData.findIndex((el) => el.id === id);

      const newArr = 
      [
        ...prevState.todoData.slice(0, idx),
        ...prevState.todoData.slice(idx + 1)
      ];

      return {
        todoData: newArr
      }

    });
  }

  ItemAdded = (NewItem) => {
    const objOfNewItem = {
      label: NewItem,
      important: false,
      id: this.maxId++
    };

    this.setState(({todoData}) => {

      return {
        todoData:  [...todoData, objOfNewItem]
      }
    });
  }

  onToggleImportant = (id) => {
    this.setState(({todoData}) => {
      const idx = todoData.findIndex((el) => el.id === id)
      const oldItem = todoData[idx];
      const NewItem = { ...oldItem, important: !oldItem.important }
      const NewArr = [
        ...todoData.slice(0, idx),
        NewItem,
        ...todoData.slice(idx + 1)
      ];

      return {
        todoData: NewArr
      }
    });
  }

  onToggleDone = (id) => {
    this.setState(({todoData}) => {
      const idx = todoData.findIndex((el) => el.id === id)
      const oldItem = todoData[idx];
      const NewItem = { ...oldItem, done: !oldItem.done }
      const NewArr = [
        ...todoData.slice(0, idx),
        NewItem,
        ...todoData.slice(idx + 1)
      ];

      return {
        todoData: NewArr
      }
    });
  }

  searchMethod = ({target: {value}}) =>  {
    this.setState((prevState) => {
      return {
        valueOfInput: value
      }
    })
  }

  filterAll = () => {
    this.setState({
      allItems: true,
      activeItems: false,
      doneItems: false 
    });
  }

  filterActive = () => {
    this.setState({
      activeItems: true,
      allItems: false,
      doneItems: false
    });
  }

  filterDone = () => {
    this.setState({ 
      doneItems: true,
      allItems: false,
      activeItems: false
    });
  }


  render() {
    console.log(this.state)
    const doneItems = this.state.todoData.filter((el) => el.done).length;
    const undoneItems = this.state.todoData.length - doneItems;

    return (
      <div className="todo-app">
        <AppHeader toDo={undoneItems} done={doneItems} />
        <div className="top-panel d-flex">
          <SearchPanel searchMethod={this.searchMethod}/>
          <ItemStatusFilter 
            filterAll={this.filterAll}
            filterActive={this.filterActive} 
            filterDone={this.filterDone}
            allItems={this.state.allItems}
            activeItems={this.state.activeItems}
            doneItems={this.state.doneItems}
            />
        </div>
        <TodoList
        
          allItems={this.state.allItems}
          activeItems={this.state.activeItems}
          doneItems={this.state.doneItems}
          todos={this.state.todoData}
          onDeleted={this.deleteItem}
          onToggleDone={this.onToggleDone}
          onToggleImportant={this.onToggleImportant}
          searchData={this.state.valueOfInput}
          />
        <ItemAddForm onItemAdded={this.ItemAdded}/>
      </div>
    );
  }

};

export default App;
