import React from 'react';

import TodoListItem from '../todo-list-item';
import './todo-list.css';

const TodoList = ({ todos, onDeleted, onToggleDone, onToggleImportant, searchData, allItems, activeItems, doneItems }) => {
  const filterTodos = todos.filter((item) => {
    console.log(activeItems);
    if(doneItems) {
      return item.label.toLowerCase().startsWith(searchData) && item.done
    } else if(activeItems) {
      return item.label.toLowerCase().startsWith(searchData) && !item.done
    } else {
      return  item.label.toLowerCase().startsWith(searchData)
    }
  })
  const elements = filterTodos.map((item) => {
    const { id, ...itemProps } = item;
    return (
      <li key={id} className="list-group-item">
        <TodoListItem 
          {...itemProps } 
          onDeleted={() => onDeleted(id) }
          onToggleImportant={() => onToggleImportant(id)}
          onToggleDone={() => onToggleDone(id)}
        />
      </li>
    );
  });

  return (
    <ul className="list-group todo-list">
      { elements }
    </ul>
  );
};

export default TodoList;
