import React, { Component } from 'react';

import './item-status-filter.css';

class ItemStatusFilter extends Component {

  render() {
    


    return (
      <div className="btn-group">
        <button type="button"
          className={`btn btn-outline-secondary ${this.props.allItems && 'btn-info'}`}
          onClick={this.props.filterAll}>All</button>
        <button type="button"
          className={`btn btn-outline-secondary ${this.props.activeItems && 'btn-info'}`}
          onClick={this.props.filterActive}>Active</button>
        <button type="button"
          className={`btn btn-outline-secondary ${this.props.doneItems && 'btn-info'}`}
          onClick={this.props.filterDone}>Done</button>
      </div>
    );
  }
};

export default ItemStatusFilter;
